<template>
	<div class="linkage">
		{{n}}--->{{$store.getters.isGood}}
		<button @click="add(5)">同步点击加2</button>
		<button @click="acyncAdd(2)">异步点击加2</button>
		<!--<slot></slot>-->
	</div>
</template>

<script>
	import {mapState,mapGetters} from "vuex"
	export default {
// 		data(){
// 			return {
// 				n:mapState(["n"])  //n:this.$store.state.n
// 			}
// 		},
		computed:{  // 跟上边一样的意思 都是将数据映射到当前组件身上
//			n(){
// 				return this.$store.state.n
// 			}	
			...mapState(["n"])
			
	   	},
	   	created(){
	   		console.log(this)
	   	},
	   	methods:{
	   		add(p){
	   			this.$store.dispatch('changeUp',p)
	   		},
	   		acyncAdd(p){
	   			this.$store.commit("Up",p)
	   		}
	   	}
	};
</script>

<style scoped>
	.linkage{
		color:red;
	}
</style>


